@use "sass:math";
@import "../../assests/scss/mixins";
@import "../../assests/scss/variables";

.faqSection {
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(rem(290), 1fr));
  gap: rem(15);
  border-top: rem(1) solid #a2a2a2;
  margin-top: rem(30);
}

.faqLeft {
  padding-top: rem(20);
}

.faqLeftDescription {
  font-weight: 400;
  font-size: rem(20);
  line-height: rem(24);
  color: $lightBlack;

  @media (max-width: 1000px) {
    font-size: clamp(rem(16), 2vw, rem(20));
    line-height: clamp(rem(20), 2.4vw, rem(24));
  }
}

.faqBlock {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  padding: rem(20) 0;

  &::after {
    display: block;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      269.61deg,
      rgba(183, 173, 211, 0.6) -15.12%,
      rgba(255, 173, 173, 0.132) 107.54%
    );
    color: #ffffff;
    opacity: 0;
    transition: opacity 0.3s;
    -webkit-transition: opacity 0.3s;
    -moz-transition: opacity 0.3s;
    -ms-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
  }

  &::before {
    display: block;
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    opacity: 1;
    width: 100%;
    height: rem(1);
    background-color: $lightBlack;
    transition: opacity 0.3s;
    -webkit-transition: opacity 0.3s;
    -moz-transition: opacity 0.3s;
    -ms-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
  }

  &:hover::after {
    opacity: 1;
  }

  &:hover::before {
    opacity: 0;
  }
}

.title {
  position: relative;
  z-index: 1;
  justify-content: space-between;
  align-items: center;
  gap: rem(20);

  h2 {
    font-weight: 500;
    font-size: rem(20);
    line-height: rem(24);
    color: $lightBlack;
    padding-right: rem(40);

    @media (max-width: 1000px) {
      font-size: clamp(rem(14), 2vw, rem(20));
      line-height: clamp(rem(18), 2.4vw, rem(24));
    }
  }
}

.iconPlus {
  position: relative;
  z-index: 1;

  &__show {
    position: relative;
    z-index: 1;
  }

  &__show::after,
  &__show::before,
  &::after,
  &::before {
    content: "";
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    display: block;
    width: rem(31);
    height: rem(1);
    background-color: $lightBlack;
    flex-grow: 0;
    flex-shrink: 0;
  }

  &__show::after,
  &::after {
    transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    -o-transform: rotate(90deg);
    right: rem(-1);
  }

  &__show::after {
    display: none;
  }
}

.content {
  height: 0;

  &__show {
    height: auto;
  }
}

.description {
  position: relative;
  z-index: 1;
  font-weight: 400;
  font-size: rem(18);
  line-height: rem(22);
  color: $lightBlack;
  padding: rem(20) 0;

  @media (max-width: 1000px) {
    font-size: clamp(rem(12), 1.8vw, rem(18));
    line-height: clamp(rem(16), 2.2vw, rem(22));
  }
}
